.hero {
  &__container {
    position: relative;
    display: flex;
    justify-content: flex-end;

    @include desktop {
      margin-right: unset;
    }
  }
}

.hero__img {
  @include mobile {
    width: 311px;
    height: 326px;
  }
}

.hero__block {
  position: absolute;

  color: $sec-txt-color;
  text-align: center;

  background-color: $bg-color;
  box-shadow: 10px 4px 10px rgba(37, 37, 46, 0.4);

  @include mobile {
    top: 76px;
    right: 38px;

    width: 321px;
    height: 268px;

    padding-top: 13px;
    padding-bottom: 17px;
    padding-left: 17px;
    padding-right: 17px;
  }

  @include desktop {
    top: 200px;
    left: -137px;

    width: 600px;
    height: 500px;

    padding-top: 120px;
    padding-bottom: 120px;
    padding-left: 92px;
    padding-right: 92px;
  }
}

.hero__name {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.8;

  @include desktop {
    margin-bottom: 20px;
    font-size: 40px;
  }
}

.hero__descr {
  font-weight: 600;
  font-size: 26px;
  line-height: 1.8;

  @include desktop {
    margin-bottom: 20px;
    font-size: 28px;
  }
}
